.caller {
  margin-bottom: 150px;
}

.caller .alert {
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.caller .card {
  max-width: 600px;
}

.caller h3 small {
  margin-left: 30px;
}
select.form-control {
  height: 5rem !important; }

.clipboardable {
  position: relative; }
  .clipboardable .clipboard-wrap {
    position: absolute;
    top: 5px;
    right: 5px;
    opacity: 0.9;
    cursor: pointer; }
  .clipboardable pre {
    background: #efefef;
    color: #444444;
    padding: 1em; }

